<template>
  <div id="login">
    <div id="content">
     <div id="dialog">
       <h1 id="title"> 天博净创信息管理系统</h1>
       <el-form id="userInfo" :model="userInfo">
         <el-form-item class="inputItem">
          <label>用户名：</label>
          <el-input class="input" v-model="userInfo.name"></el-input>
         </el-form-item>
         <el-form-item class="inputItem" style="height:20px">
          <label>密&nbsp;&nbsp;&nbsp;&nbsp;码：</label>
          <el-input class="input" v-model="userInfo.password" type="password"></el-input>
         </el-form-item>
         <el-form-item style="margin-left:32%;height:30px">
           <el-checkbox style="color:#000000; font-weight:bold" v-model="checked">记住密码</el-checkbox>
         </el-form-item>
         <el-form-item id="buttonItem">
           <el-button round class="Button" style="margin-left:80px" @click="submitBtn">登  录 </el-button>
           <el-button round class="Button" style="margin-left:125px" @click="cancleBtn">取  消 </el-button>
         </el-form-item>
       </el-form>
      </div>
     </div>
  </div>
</template>

<script>
import { param } from './serial'
export default {
  data () {
    return {
      userInfo: {
        name: '',
        password: ''
      },
      checked: false
    }
  },
  methods: {
    cancleBtn () {
      this.userInfo.name = ''
      this.userInfo.password = ''
    },
    submitBtn (userInfo) {
      var name = this.userInfo.name
      var pwd = this.userInfo.password
      if (name === '' || name === null) {
        alert('请输入用户名!')
        return
      }
      if (pwd === '' || pwd === null) {
        alert('请输入密码!')
        return
      }
      if (this.checked === true) {
        this.clearCookie()
        this.setCookie(name, pwd)
      }
      this.axios.post('http://192.168.1.108:8000/login/', param(this.userInfo) ).then((res) => {
        alert(res.data)
      })
    },
    setCookie (_name, _pwd) {
      window.document.cookie = 'NAME' + '=' + _name
      window.document.cookie = 'PASSWORD' + '=' + _pwd
    },
    getCookie () {
      if (document.cookie.length > 0) {
        var info = document.cookie.split(';')
        for (var i = 0; i < info.length; i++) {
          var item = info[i].split('=')
          alert(item[0])
          alert(item[1])
          if (item[0] === 'PASSWORD') {
            this.userInfo.password = item[1]
          }
        }
      }
    },
    clearCookie () {
      this.setCookie('', '')
    }
  }
}
</script>

<style scoped>
#login {
  position: fixed;
  width: 100%;
  height: 100%;
  top: 0px;
  background-image: url("./assets/login.jpg");
  background-repeat:no-repeat;
  background-size: cover;
}
#content {
  padding-top: 10%;
  width: 100%;
}
#dialog {
  width: 450px;
  height: 300px;
  border: 3px solid #ffffff;
  margin: 0 auto;
  top: 20%;
  background: -webkit-linear-gradient(top, white, lightblue, skyblue)
}
#title {
  font-family: Cambria, Cochin, Georgia, Times, 'Times New Roman', serif;
  color:black;
  text-align: center
}
#userInfo{
  color: black;
  font-family:Georgia, 'Times New Roman', Times, serif;
  font-weight: bold
}
.inputItem {
  text-align: center;
  height: 35px;
}
.input {
  width: 230px;
  height: 15px;
}
.Button {
  width: 80px;
  height: 30px;
  text-align: center;
  font-family: 'Segoe UI', Tahoma, Geneva, Verdana, sans-serif;
  font-weight: bold;
  padding: 4px 15px
}

</style>



